  }

  .item-list-group{
    margin-top: 20px;
    box-shadow:4px 10px 20px #171e49;
    box-shadow:4px 10px 20px var(--boxshadow);
    border-radius: 20px;
    padding: 10px;
  }

  .item-title{
  display: inline-block;
  font-size: 22px;
  }
  .item-title:hover{
    cursor: pointer;
    color:#ff4699;
    color:var(--pink);
  }

  .item-title-group{
    display: flex;
    justify-content:center;
  }
.level{
    margin: 0;
}


input[type=range] {
    -webkit-appearance: none;
    margin: 10px 0;
    width: 70%;
  }
 
  input[type=range]::-webkit-slider-runnable-track {
    height: 5px;
    cursor: pointer;
    background: #fbf5f4;
    background: var(--white);
  }

  input[type=range]::-webkit-slider-thumb {
    height: 20px;
    width: 10px;
    border-radius: 9px;
    background:#ff4699;
    background:var(--pink);
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -5.5px;
  }

  .check-list{
    display: flex;
    justify-content: center;
    margin:10px;
  }

  .theorem{
    margin-left: 10px;
  }

  .green-icon{
    color:#2CE77A;
    color:var(--green);
    font-size:50px;
  }

  .red-icon{
    color:#ff9b71;
    color:var(--orange)
  }

